.App {
  text-align: center;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.title {
  margin: 8px;
}

.container-start {
  display: flex;
  flex-direction: column;
}

.radio-custom {
  width: 100%;
  display: flex;
  flex-direction: row;
}

.start-button {
  margin: 4px;
  height: 32px;
  width: 416px;
  font-size: 120%;
}

.categories-container {
  display: flex;
  flex-direction: row;
  width: calc(100% - 8px);
  padding-left: 4px;
  padding-right: 4px;
}

.category-list {
  display: flex;
  flex-direction: column;
  flex: 1 1 200px;
  padding-top: 16px;
  padding-bottom: 16px;
  overflow: auto;
  align-items: center;
}

.question-container {
  width: 100%;
  max-width: 420px;
  height: 100%;
}

.separator {
  width: 0px;
  border-radius: 4px;
  border: 4px solid black;
  margin-top: 16px;
  margin-left: 8px;
  margin-right: 8px;
  margin-bottom: 16px;
}

.example {
  position: relative;
  width: 160px;
  height: 160px;
  max-width: 100%;
  background-color: #333;
  border-radius: 8px;
  margin-top: 4px;
  margin-bottom: 4px;
  box-shadow: 0px 0px 8px 0px #666;
  padding: 1px;
}

.example-image {
  position: relative;
  height: auto;
  width: auto;
  max-height: 100%;
  max-width: 100%;
  top: 50%;
  transform: translate(0, -50%);
  background-color: #ddd;
  border-radius: 8px;
}

.answer-container {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
}

.answer-example {
  width: 50%;
}

.div-button {
  width: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fcfcfc;
  border-radius: 2px;
  padding: 12px;
  margin-top: 16px;
  margin-left: 16px;
  margin-right: 16px;
  box-shadow: 0px 0px 8px 0px #666;
  font-size: 56px;
  cursor: pointer;
}

.div-button:active {
  box-shadow: 0px 0px 0px 0px #666;
}

.instructions {
  background-color: #eee;
  border-radius: 8px;
  padding: 12px;
  padding-top: 16px;
  padding-bottom: 16px;
  width: 392px;
  font-size: 120%;
  margin: 4px;
  margin-bottom: 16px;
  box-shadow: 0px 0px 8px 0px #666;
  text-align: left;
}

.button-red {
  color: red;
}

.button-green {
  color: green;
}

.hint-container {
  font-size: 120%;
  margin-bottom: 8px;
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #0000ff33;
  border: 3px solid blue;
  border-radius: 6px;
  padding: 8px;
}

.hint-button {
  width: 64px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #fcfcfc;
  border-radius: 2px;
  padding: 4px;
  margin-top: 8px;
  box-shadow: 0px 0px 8px 0px #666;
  font-size: 24px;
  cursor: pointer;
}

.hint-button:active {
  box-shadow: 0px 0px 0px 0px #666;
}

.username {
  position: absolute;
  right: 2px;
  top: 2px;
  font-size: 6px;
}

/* class applies to select element itself, not a wrapper element */
select {
  margin: 4px;
  height: 42px;
  width: 416px;

  display: block;
  font-size: 16px;
  width: 416px;
  box-sizing: border-box;
  border: 1px solid #aaa;
  box-shadow: 0 1px 0 1px rgba(0, 0, 0, 0.04);
  padding: 8px;
  border-radius: 4px;
  -moz-appearance: none;
  -webkit-appearance: none;
  appearance: none;
  background-color: #fff;
  /* note: bg image below uses 2 urls. The first is an svg data uri for the arrow icon, and the second is the gradient. 
		for the icon, if you want to change the color, be sure to use `%23` instead of `#`, since it's a url. You can also swap in a different svg icon or an external image reference
		
	*/
  background-image: url("data:image/svg+xml;charset=US-ASCII,%3Csvg%20xmlns%3D%22http%3A%2F%2Fwww.w3.org%2F2000%2Fsvg%22%20width%3D%22292.4%22%20height%3D%22292.4%22%3E%3Cpath%20fill%3D%22%23444444%22%20d%3D%22M287%2069.4a17.6%2017.6%200%200%200-13-5.4H18.4c-5%200-9.3%201.8-12.9%205.4A17.6%2017.6%200%200%200%200%2082.2c0%205%201.8%209.3%205.4%2012.9l128%20127.9c3.6%203.6%207.8%205.4%2012.8%205.4s9.2-1.8%2012.8-5.4L287%2095c3.5-3.5%205.4-7.8%205.4-12.8%200-5-1.9-9.2-5.5-12.8z%22%2F%3E%3C%2Fsvg%3E");
  background-repeat: no-repeat;
  /* arrow icon position (1em from the right, 50% vertical) , then gradient position*/
  background-position: right 0.7em top 50%;
  /* icon size, then gradient */
  background-size: 0.65em auto;
}
/* Hide arrow icon in IE browsers */
select::-ms-expand {
  display: none;
}
/* Hover style */
select:hover {
  border-color: #888;
}
/* Focus style */
select:focus {
  border-color: #aaa;
  /* It'd be nice to use -webkit-focus-ring-color here but it doesn't work on box-shadow */
  box-shadow: 0 0 1px 3px rgba(64, 64, 64, 0.7);
  box-shadow: 0 0 0 3px -moz-mac-focusring;
  color: #222;
  outline: none;
}

/* Set options to normal weight */
select option {
  font-size: 16px;
  font-weight: normal;
}
